import React from 'react'
import { FaTimes } from 'react-icons/fa';
import styles from '../Styles/Market.module.css'
import {useState} from "react"
function Market() {
   
    const getLocal = JSON.parse(localStorage.getItem('products')) 
    const [getItem, setItem2] = useState(getLocal ?? []) 
    const handleDelete =  (i) => {
            setItem2((prev)=> {
                const a = [...prev]
                a.splice(i,1)
                localStorage.setItem('products', JSON.stringify(a))
                return a
            })
    } 
    return (
        <div className="containermarket">
            <div className={styles.market}>
            {getItem.map((item,i) => 
                 <div key={i}className={styles.market_body}>
                 <img className={styles.market_img} src={item.url} alt="img"></img>
                 <div className={styles.marketitem}>
                     <p className={styles.markettext2}>{item.title}</p>
                     <p className={styles.markettext}>{item.price}</p>

                     <div className={styles.market_btn}>
                         <button className={styles.marketbtnincrease}>-</button>
                         <p>1</p>
                         <button className={styles.marketbtnincrease}>+</button>
                     </div>
                 </div>

                 <div className={styles.market_footer}>
                 <div onClick={()=>handleDelete(i)}>
                 <span className={styles.time}><FaTimes /></span>
                 </div>
                     <p className={styles.markettext2}>{item.price}</p>
                     
                 </div>
                 

             </div>
            )}
               
                    
                <span className={styles.hr}></span>
                <div className={styles.markettotal}>
                    <div className={styles.total}>
                        <p className={styles.tong}>Tổng cộng:</p>
                        <p className={styles.totalprice}>80.000d</p>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Market